import axios from 'axios';
import { notification } from 'antd';
import md5 from 'md5';
import history from '../history';
import globalStore from '../stores/global';

const service = axios.create({
  timeout: 30000,
  cache: false,
});

// timestamp 
// token
// #AAA 
// axios.defaults.headers.common.Authorization =
// 'Basic timestamp=13123123123,token=tiket,sign=md5(timestamp+#AAA)';

//request 
service.interceptors.request.use(
  config => {
    const token = sessionStorage.getItem('token');

    // tokenhttp headertoken
    if (token) {
      // 
      const secret = '#GMS';
      // --
      const timestamp = (new Date()).getTime().toString();
      // 
      const sign = md5(timestamp + secret);

      config.headers.Authorization = `Basic timestamp=${timestamp},token=${token},sign=${sign}`;
      
      //  MenuID
      if(config.method === 'get') {
        if(!config.params) {
          config.params = {};
        }
        if(globalStore.selectedKeys.length > 0) {
          config.params.MenuID = globalStore.selectedKeys[0];
        }
      } else {
        if(!config.data) {
          config.data = {};
        }
        if(globalStore.selectedKeys.length > 0) {
          config.data.MenuID = globalStore.selectedKeys[0];
        }
      }
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// response 
service.interceptors.response.use(
  response => {
    if (response.status === 200 || response.status === 304) {
      const { data } = response;

      if (data.Code === 200 && response.headers.authorization) {
        //  token
        sessionStorage.setItem('token', response.headers.authorization);
      }

      if (data.Code === 101) { // 
        //  data.Error.ModelState 
      } else if (data.Code === 100 || data.Code === 104) {
        //  localstorage  user  token
        sessionStorage.removeItem('currentUser');
        sessionStorage.removeItem('token');

        // 
        history.push('/login');
      } else if (data.Code !== 200) {
        notification.error({
          message: `Code: ${data.Code}`,
          description: data.Error.Message,
        });
      }
      return data;
    } else {
      notification.error({
        message: ` ${response.status}: ${response.url}`,
        description: response.statusText,
      });
      return Promise.reject('error');
    }
  },
  error => {

    notification.error({
      message: `error`,
      description: error.message,
    });
    return Promise.reject(error);
  }
);

export default service;



