import { observable, action } from 'mobx';

import { queryTree, queryList, insert, remove, queryDetail, update } from '@/services/SystemManagement/menu';
import { queryMenuButton } from '@/services/global';

class MenuStore {
  // 
  @observable authMenuListButton;
  @observable authMenuFormButton;

  // 
  @observable treeList; 
  // id
  @observable selectedKeys; 

  // 
  @observable list; 
  // 
  @observable loading; 
  // 
  // @observable pagination;
  // 
  @observable selectedRowKeys;
  // 
  @observable currentDetail;
  
  // currentForm  clear 
  defaultCurrentForm = {
    IsDisplayed: {
      value: 1,
    },
  };
  // eg: {Name: {value: 'test'}},
  @observable currentForm;
  // 
  @observable newBtnLoading;


  @action
  async fetchAuthMenuListButton(data) {
    const response = await queryMenuButton({
      Category: 1,
    });
    if(response.Code === 200) {
      this.setData({
        authMenuListButton: response.Data,
      });
    }
  }

  @action
  async fetchAuthMenuFormButton(data) {
    const response = await queryMenuButton({
      Category: 2,
    });
    if(response.Code === 200) {
      this.setData({
        authMenuFormButton: response.Data,
      });
    }
  }

  /**
   *  action
   */
  @action
  async commit(data) {

    this.setData({
      newBtnLoading: true,
      error: null,
    });

    let response = null;

    //  id 
    if(data.UniqueID) {
      response = await update(data);
    } else {
      response = await insert(data);
      this.setCurrentFormField({
        UniqueID: {
          name: 'UniqueID', 
          value: response.Data,
        },
      })
    }

    this.setData({
      newBtnLoading: false,
    });

    if(response.Code === 200) {
      this.refreshList();
      this.fetchTree();
    } else {
      return await Promise.reject(response.Error);
    }
  }

  @action
  async remove(data) {
    const response = await remove(data);
    if(response.Code === 200) {
      this.refreshList();
      this.fetchTree();
    } else {
      return await Promise.reject(response.Error);
    }
  }

  @action
  async fetchDetail() {

    const response = await queryDetail({
      UniqueID: this.selectedRowKeys[0],
    });

    if (response.Code === 200) {
      this.setData({
        currentDetail: response.Data,
      });
    } else {
      return await Promise.reject(response.Error);
    }
  }

  // 
  @action
  async fetchTree() {
    const response = await queryTree();
    if (response.Code === 200) {
      this.setData({
        treeList: response.Data
      });
    }
  }

  // 
  @action
  async refreshList() {
    this.fetchList({
      ParentID: this.selectedKeys[0],
      // CurrentPage: this.pagination.current,
      // PageSize: this.pagination.pageSize,
    });
  }

  @action
  async fetchList(data) {
    this.setData({
      loading: true,
    });

    const response = await queryList(data);

    if (response.Code === 200) {

      this.setData({
        list: response.Data,
        // total 
        // pagination: {
        //   ...this.pagination,
        //   total: response.TotalCount
        // },
      });
    }

    this.setData({
      loading: false,
    });
  }

  /**
   *  action
   */
  @action
  setData(data) {
    Object.keys(data).forEach((key) => {
      this[key] = data[key];
    });
  }
  // 
  @action
  reset() {
    // 
    this.authMenuListButton = [];
    this.authMenuFormButton = [];
    
    // 
    this.treeList = []; 
    // id
    this.selectedKeys = ['0']; 

    // 
    this.list = []; 
    // 
    this.loading = false; 
    // 
    // this.pagination = {
    //   current: 1,
    //   pageSize: 200,
    //   total: 0, // ,
    // };
    // 
    this.selectedRowKeys = [];
    // 
    this.currentDetail = null;
    
    // eg: {Name: {value: 'test'}},
    this.currentForm = this.defaultCurrentForm;
    // 
    this.newBtnLoading = false;
  }

  @action
  clearCurrentForm() {
    this.currentForm = this.defaultCurrentForm;
    this.error = null;
  }
  @action
  setCurrentFormField(data) {
    this.currentForm = {
      ...this.currentForm,
      ...data,
    }
  }
  @action
  setCurrentForm(data) {
    // 
    Object.keys(data).forEach((key) => {
      data[key] = {
        name: key, 
        value: data[key],
      };
    });

    this.currentForm = data;
  }
}

export default new MenuStore();



