import { observable, action } from 'mobx';

import { insert, update, remove, queryList, queryDetail, queryMenuButtonTree } from '@/services/SystemManagement/menuButton';

import menu from './menu';

class MenuButtonStore {

  // 
  @observable list;
  // 
  @observable loading; 
  // 
  // @observable pagination;

  // currentForm  clear 
  defaultCurrentForm = {};
  // eg: {Name: {value: 'test'}},
  @observable currentForm;
  // 
  @observable newBtnLoading;
  // 
  @observable menuButtonTree;

   /**
   *  action
   */
  @action
  async commit(data) {

    this.setData({
      newBtnLoading: true,
      error: null,
    });

    let response = null;

    //  id 
    if(data.UniqueID) {
      response = await update(data);
    } else {
      response = await insert(data);
    }

    this.setData({
      newBtnLoading: false,
    });

    if(response.Code === 200) {
      this.refreshList();
    } else {
      return await Promise.reject(response.Error);
    }
  }

  @action
  async remove(data) {
    const response = await remove(data);
    if(response.Code === 200) {
      this.refreshList();
    } else {
      return await Promise.reject(response.Error);
    }
  }

  @action
  async fetchDetail(data) {

    const response = await queryDetail(data);

    if (response.Code === 200) {
      const data = {};
      // 
      Object.keys(response.Data).forEach((key) => {
        let value = response.Data[key];
        if(key==='ParentID') {
          value = value.toString();
        }

        data[key] = {
          name: key,
          value,
        };
      });

      this.setData({
        currentForm: {...data},
      });
    } else {
      return await Promise.reject(response.Error);
    }
  }

  // 
  @action
  async refreshList() {
    this.fetchList({
      Menu_ID: menu.currentForm.UniqueID.value,
    });
  }

  @action
  async fetchList(data) {
    this.setData({
      loading: true,
    });

    const response = await queryList(data);

    if (response.Code === 200) {

      this.setData({
        list: response.Data,
      });
    }

    this.setData({
      loading: false,
    });
  }

  @action
  async fetchMenuButtonTree(data) {
    const response = await queryMenuButtonTree(data);
    if(response.Code === 200) {
      this.setData({
        menuButtonTree: this.formatTree(response.Data),
      });
    }
  }

  formatTree(treeList) {
    return treeList.map(item => {
      if(item.hasChildren && item.children) {
        item.children = this.formatTree(item.children);
      }
      return {
        ...item,
        value: item.id,
        key: item.id,
        label: item.name,
      };
    })
  }

  /**
   *  action
   */
  @action
  setData(data) {
    Object.keys(data).forEach((key) => {
      this[key] = data[key];
    });
  }
  // 
  @action
  reset() {
    // 
    this.list = [];
    // 
    this.loading = false; 

    // eg: {Name: {value: 'test'}},
    this.currentForm = this.defaultCurrentForm;
    // 
    this.newBtnLoading = false;
    // 
    this.menuButtonTree = [];
  }

  @action
  clearCurrentForm() {
    this.currentForm = this.defaultCurrentForm;
    this.error = null;
  }
  @action
  setCurrentFormField(data) {
    this.currentForm = {
      ...this.currentForm,
      ...data,
    }
  }
} 

export default new MenuButtonStore();




