import { observable, action } from 'mobx';
import history from '../history';

import { login } from '../services/currentUser';

class CurrentUserStore {
  @observable currentUser = null; // ,
  @observable submitting = false; // ;
  @observable error = null; //
  /**
   *  action
   */
  
  @action
  async login(data) {

    this.setData({
      submitting: true,
    });
    
    const response = await login(data);

    if(response.Code === 200) {
      const user = response.Data;

      //  localStorage
      if(data.remember) {
        localStorage.setItem('loginName', data.LoginName);
        localStorage.setItem('loginPwd', data.LoginPwd);
        localStorage.setItem('remember', data.remember);
      } else {
        localStorage.removeItem('loginName');
        localStorage.removeItem('loginPwd');
        localStorage.removeItem('remember');
      }
      // token  token
      sessionStorage.setItem('token', user.Token);
      delete user.Token;

      //  sessionStorage 
      sessionStorage.setItem('currentUser', JSON.stringify(user));

      this.setData({
        currentUser: response.Data,
        error: null,
        submitting: false,
      })

      history.push('/basic');
    } else {
      this.setData({
        error: response.Error,
        submitting: false,
      });
      return await Promise.reject(response.Error);
    }
  }

  /**
   *  action
   */
  @action
  setData(data) {
    Object.keys(data).forEach((key) => {
      this[key] = data[key];
    });
  }

  @action
  logout() {
    this.setData({
      currentUser: null,
    });
    
    //  sessionStorage  user  token
    sessionStorage.removeItem('currentUser');
    sessionStorage.removeItem('token');

    history.push('/login');
  }

  @action
  loadCurrentUserFromSession() {
    const user = JSON.parse(sessionStorage.getItem('currentUser'));
    this.currentUser = user;
  }
}

export default new CurrentUserStore();


