import { observable, action } from 'mobx';

import { queryList, remove } from '@/services/SystemManagement/exceptionLog';
import { queryMenuButton } from '@/services/global';

class ExceptionLogStore {
  // 
  @observable authMenuListButton;
  @observable authMenuFormButton;

  // 
  @observable list;
  // 
  @observable loading; 
  // 
  @observable pagination;
  // table 
  @observable orderField;
  // 
  @observable isDesc;
  // 
  @observable selectedRowKeys;

  // 
  @observable searchFormValues;
  // 
  @observable operateTypeTextValue;
  

  @action
  async fetchAuthMenuListButton(data) {
    const response = await queryMenuButton({
      Category: 1,
    });
    if(response.Code === 200) {
      this.setData({
        authMenuListButton: response.Data,
      });
    }
  }

  @action
  async fetchAuthMenuFormButton(data) {
    const response = await queryMenuButton({
      Category: 2,
    });
    if(response.Code === 200) {
      this.setData({
        authMenuFormButton: response.Data,
      });
    }
  }

  /**
   *  action
   */
  @action
  async remove(data) {
    const response = await remove(data);
    if(response.Code === 200) {
      this.refreshList();
    } else {
      return await Promise.reject(response.Error);
    }
  }

  // 
  @action
  async refreshList() {
    let orderData = {};
    if(this.orderField) {
      orderData = {
        OrderField: this.orderField,
        IsDesc: this.isDesc,
      }
    }
    
    this.fetchList({
      CurrentPage: this.pagination.current,
      PageSize: this.pagination.pageSize,
      ...this.searchFormValues,
      ...orderData,
    });
  }

  @action
  async fetchList(data) {
    this.setData({
      loading: true,
    });

    const response = await queryList(data);

    if (response.Code === 200) {
      this.setData({
        list: response.Data,
        pagination: {
          ...this.pagination,
          total: response.TotalCount,
        },
      });
    }

    this.setData({
      loading: false,
    });
  }

  /**
   *  action
   */
  @action
  setData(data) {
    Object.keys(data).forEach((key) => {
      this[key] = data[key];
    });
  }
  // 
  @action
  reset() {
    // 
    this.authMenuListButton = [];
    this.authMenuFormButton = [];
    
    // 
    this.list = [];
    // 
    this.loading = false; 
    // 
    this.pagination = {
      current: 1,
      pageSize: 20,
      total: 0, // ,
    };
    // table 
    this.orderField = null;
    // 
    this.isDesc = false;
    // 
    this.selectedRowKeys = [];

    // 
    this.searchFormValues = {};
    // 
    this.operateTypeTextValue = [];
  }
}

export default new ExceptionLogStore();



