import { observable, action } from 'mobx';

import { queryTree, queryList, insert, remove, queryDetail, update, resetPwd, setStatus, getMemberRole, setMemberRole } from '@/services/OrgManagement/user';
import { queryMenuButton, queryMenuList } from '@/services/global';

class UserStore {

  // 
  @observable authMenuListButton;
  @observable authMenuFormButton;
  // 
  @observable authMenuListField;

  // 
  @observable treeList; 
  // id
  @observable selectedKeys;

  // 
  @observable list; 
  // 
  @observable loading; 
  // 
  @observable pagination;
  // 
  @observable selectedRowKeys;
  // 
  @observable searchFormValues;
  // table 
  @observable orderField;
  // 
  @observable isDesc;
  // 
  @observable currentDetail;
  
  // currentForm  clear 
  defaultCurrentForm = {};
  // eg: {Name: {value: 'test'}},
  @observable currentForm;
  // 
  @observable newBtnLoading;
  // 
  @observable orgTextValue;

  // 
  @observable memberRole;
  // 
  @observable roleSelectedRowKeys;


  @action
  async fetchAuthMenuListButton(data) {
    const response = await queryMenuButton({
      Category: 1,
    });
    if(response.Code === 200) {
      this.setData({
        authMenuListButton: response.Data,
      });
    }
  }
  @action
  async fetchAuthMenuFormButton(data) {
    const response = await queryMenuButton({
      Category: 2,
    });
    if(response.Code === 200) {
      this.setData({
        authMenuFormButton: response.Data,
      });
    }
  }
  @action
  async fetchAuthMenuListField(data) {
    const response = await queryMenuList();
    if(response.Code === 200) {
      this.setData({
        authMenuListField: response.Data,
      });
    }
  }
  

  /**
   *  action
   */
  @action
  async commit(data) {

    this.setData({
      newBtnLoading: true,
      error: null,
    });

    let response = null;

    //  id 
    if(data.UniqueID) {
      response = await update(data);
    } else {
      response = await insert(data);
    }

    this.setData({
      newBtnLoading: false,
    });

    if(response.Code === 200) {
      this.refreshList();
    } else {
      return await Promise.reject(response.Error);
    }
  }

  @action
  async remove(data) {
    const response = await remove(data);
    if(response.Code === 200) {
      this.refreshList();
    } else {
      return await Promise.reject(response.Error);
    }
  }

  @action
  async fetchDetail() {

    const response = await queryDetail({
      UniqueID: this.selectedRowKeys[0],
    });

    if (response.Code === 200) {
      this.setData({
        currentDetail: response.Data,
      });
    } else {
      return await Promise.reject(response.Error);
    }
  }

  // 
  @action
  async fetchTree() {
    const response = await queryTree();
    if (response.Code === 200) {
      this.setData({
        treeList: response.Data,
      });

      return response.Data;
    } else {
      return await Promise.reject(response.Error);
    }
  }

  // 
  @action
  async refreshList() {
    let orderData = {};
    if(this.orderField) {
      orderData = {
        OrderField: this.orderField,
        IsDesc: this.isDesc,
      }
    }
    
    this.fetchList({
      OrganizationID: this.selectedKeys[0],
      CurrentPage: this.pagination.current,
      PageSize: this.pagination.pageSize,
      ...this.searchFormValues,
      ...orderData,
    });
  }

  @action
  async fetchList(data) {
    this.setData({
      loading: true,
    });

    const response = await queryList(data);

    if (response.Code === 200) {
      this.setData({
        list: response.Data,
        pagination: {
          ...this.pagination,
          total: response.TotalCount,
        },
      });
    }

    this.setData({
      loading: false,
    });
  }

  // 
  @action
  async resetPwd(data) {
    const response = await resetPwd(data);

    if(response.Code !== 200) {
      return await Promise.reject(response.Error);
    }
  }

  // /
  @action
  async setStatus(data) {
    const response = await setStatus(data);

    if(response.Code === 200) {
      this.refreshList();
    } else {
      return await Promise.reject(response.Error);
    }
  }

  // 
  @action
  async getMemberRole(data) {
    const response = await getMemberRole(data);

    if(response.Code === 200) {
      const roleSelectedRowKeys = 
      response.Data
      .filter(item => item.IsMemberRole)
      .map(item => {
        return item.UniqueID;
      });

      this.setData({
        memberRole: response.Data,
        roleSelectedRowKeys,
      });
    } else {
      return await Promise.reject(response.Error);
    }
  }

  // 
  @action
  async setMemberRole(data) {
    const response = await setMemberRole(data);

    if(response.Code !== 200) {
      // response.Error.Message
      return await Promise.reject(response.Error);
    }
  }
  
  /**
   *  action
   */
  @action
  setData(data) {
    Object.keys(data).forEach((key) => {
      this[key] = data[key];
    });
  }
  // 
  @action
  reset() {
    // 
    this.authMenuListButton = [];
    this.authMenuFormButton = [];

    // 
    this.treeList = []; 
    // id
    this.selectedKeys = [];

    // 
    this.list = []; 
    // 
    this.loading = false; 
    // 
    this.pagination = {
      current: 1,
      pageSize: 20,
      total: 20, // ,
    };
    // 
    this.selectedRowKeys = [];
    // 
    this.searchFormValues = {};
    // table 
    this.orderField = null;
    // 
    this.isDesc = false;
    // 
    this.currentDetail = null;
    
    // eg: {Name: {value: 'test'}},
    this.currentForm = this.defaultCurrentForm;
    // 
    this.newBtnLoading = false;
    // 
    this.orgTextValue = [];

    // 
    this.memberRole = null;
    // 
    this.roleSelectedRowKeys = [];
  }

  @action
  clearCurrentForm() {
    this.currentForm = this.defaultCurrentForm;
    this.error = null;
  }
  @action
  setCurrentFormField(data) {
    this.currentForm = {
      ...this.currentForm,
      ...data,
    }
  }
  @action
  setCurrentForm(data) {
    // 
    Object.keys(data).forEach((key) => {
      data[key] = {
        name: key, 
        value: data[key],
      };
    });

    this.currentForm = data;
  }
}

export default new UserStore();



