import React, { Component } from 'react';
import { Provider } from 'mobx-react';
import * as mobx from 'mobx';
// react-router-dom  react-router DOM <Link> <BrowserRouter> 
import { Router, Route, Switch, Redirect } from 'react-router-dom';
import { LocaleProvider } from 'antd';
import zhCN from 'antd/lib/locale-provider/zh_CN';
import moment from 'moment';
import 'moment/locale/zh-cn';


import stores from './stores';
import routerConfig from "./router";
import history from './history';

// mobx  action 
mobx.useStrict(true);

//  locale
moment.locale('zh-cn');

class App extends Component {

  componentWillMount() {
    //  session 
    stores.currentUser.loadCurrentUserFromSession();
  }

  render() {
    return (
      <Provider {...stores}>
        <LocaleProvider locale={zhCN}>
          <Router history={history}>
            <Switch>
              {
                routerConfig.map(item => (
                  <Route path={item.path} key={item.path} exact={item.exact} component={ item.component } />
                ))
              }
              <Redirect from="/" to={stores.currentUser.currentUser ? '/basic' : '/login'} />
            </Switch>
          </Router>
        </LocaleProvider>
      </Provider>
    );
  }
}

export default App;


